import React, { Component } from 'react';
import style from './machine.css';
import cx from 'classnames';




export default class Machine extends Component {

  constructor(props) {
    super(props);
    
    this.state = {
      numLetters: 10,
      tape: this.buildTape(10),
      numStates: 1,
      currentState: 0,
      tapePosition: this.getTapePosition(10),
      done: false
    };

    console.log(this.state);
    

    this.state.table = this.buildTable(this.state.numStates, this.state.tape.length);
    
    for (let i = 0; i < this.state.numStates; i++) {
      for (let j = 0; j < this.state.tape.length; j++) {

        this.state.table[i][j] = {
          direction: sample(['L', 'R', 'S']),
          nextState: 0,
          symbol: getRandomInt(0, this.state.numLetters)
        };

      }
    }
  }

  buildTape(n) {
    let tape = [];
    
    for (let i = 0; i < n; i++) {
      tape.push(getRandomInt(0, n - 1));
    }
    return tape;
  }

  buildTable(numStates, tapeLength) {
    let table = Array(numStates).fill([]);
    
    for (let i = 0; i < numStates; i++) {
      for (let j = 0; j < tapeLength; j++) {
        table[i][j] = {
          direction: 'L',
          nextState: 0,
          symbol: getRandomInt(0, this.state.numLetters)
        };

      }
    }
    return table;
  }

  getTapePosition(n) {
    return getRandomInt(0, n - 1);
  }

  
  

  handleChange = (field, e) => {
    let tape = this.state.tape;
    let table = this.state.table;

    if (field === 'numLetters') {
      tape = this.buildTape(+e.target.value);
      table = this.buildTable(this.state.numStates, tape.length);
    }

    
    this.setState({
      [field]: e.target.value,
      tape: tape,
      table: table
    });

    

  }

  

  onChangeDirection = (row, col, e) => {
    this.state.table[row][col].direction = e.target.value;
    this.setState(this.state);
  }

  onChangeSymbol = (row, col, e) => {
    this.state.table[row][col].symbol = e.target.value;
    this.setState(this.state);
  }

  onChangeState = (row, col, e) => {
    this.state.table[row][col].nextState = e.target.value;
    this.setState(this.state);
  }

  handleRun = () => {
    if (this.state.done) return;
    
    const letter = this.state.tape[this.state.tapePosition];

    if (!this.state.table[this.state.currentState] || ! this.state.table[this.state.currentState][letter]) {
      this.setState({
        done: true
      });
      return;

    }

    const op = this.state.table[this.state.currentState][letter];
    
    const { direction, symbol, nextState } = op;

    
    let newTapePosition = this.state.tapePosition;

    switch(true) {
    case direction === 'L':
      newTapePosition--;
      break;
    case direction === 'R':
      newTapePosition++;
      break;
    default:
    }
    

    this.state.tape[this.state.tapePosition] = symbol;
    
    this.setState({
      tapePosition: newTapePosition,
      tape: this.state.tape,
      currentState: nextState,
      done: nextState === -1
    });
  }

  handleRunWithTimer = () => {
    if (this.state.done) {
      return;
    }
    this.handleRun();
    this._timer = setTimeout(this.handleRunWithTimer, 1000);
  }

  handleBuildRandom = () => {
    this.setState({
      ...this.state,
      tape: this.buildTape(this.state.tape.length),
      tapePosition: this.getTapePosition(this.state.tape.length),
      done: false
    }, () => clearInterval(this._timer));
    
  }

  renderInputs() {
    return (
      <div className={style['inputs']}>
        <label style={{display: 'block'}}>
          Алфавит
          <input type="text" value={this.state.numLetters} onChange={this.handleChange.bind(this, "numLetters")}/>
        </label>
        <label style={{display: 'block'}}>
          Число состояний
          <input type="text" value={this.state.numStates} onChange={this.handleChange.bind(this, "numStates")}/>
        </label>
        <label style={{display: 'block'}}>
          Стартовое состояние
          <input type="text" value={this.state.currentState} onChange={this.handleChange.bind(this, "currentState")}/>
        </label>
      </div>
    );
  }

  renderCell(row, col) {
    const cell = this.state.table[row][col];
    return (
      <>
        <input onChange={this.onChangeDirection.bind(this, row, col)} value={cell.direction} />
        <input onChange={this.onChangeSymbol.bind(this, row, col)} value={cell.symbol}/>
        <input onChange={this.onChangeState.bind(this, row, col)} value={cell.nextState}/>
      </>
    );
  }

  renderTable() {
    let rows = [];
    let columns = []

    for (let i = 0; i < this.state.numLetters; i++) {
      columns.push(i);
    }

    for (let i = 0; i < this.state.numStates; i++) {
      rows.push(i);
    }


    return (
      <div className={style['table-wrap']}>
        <table>
          <thead>
            {
              <tr>{columns.map(column => <td key={column}>{column}</td>)}</tr>
            }
          </thead>
          <tbody>
            {
              rows.map((row) => <tr key={row}>{columns.map((column) => <td key={row+column}>{this.renderCell(row, column)}</td>)}</tr>)
            }
          </tbody>
        </table>
      </div>
    )
  }
  
  renderTape() {
    return (
      <div className={style["tape"]}>
        {
          this.state.tape.map((a, index) => {
            return index === this.state.tapePosition ? <div key={index} className={cx(style.letter, style.current)}>{a}</div> : <div key={index} className={style["letter"]}>{a}</div>
          })
        }
      </div>
    );
  }

  renderCommandPanel() {
    return (
      <div className={style.commands}>
        <button onClick={this.handleRun}>Run</button>
        <button onClick={this.handleRunWithTimer}>Run with timer</button>
        <button onClick={this.handleBuildRandom}>Build random</button>
        <div className="info">
          <h3>Command</h3>
          <div>Direction: {}</div>
        </div>
      </div>
    );
  }

  renderMessages() {
    return this.state.done ? "Программа завершена" : null;
  }

  render() {
    return (
      <>
        {this.renderMessages()}
        {this.renderTape()}
        {this.renderCommandPanel()}
        {this.renderInputs()}
        {this.renderTable()}
      </>
    );
      
  }
}

function sample(a) {
  return a[Math.floor(Math.random() * a.length)];
}

function getRandomArbitrary(min, max) {
  return Math.random() * (max - min) + min;
}

function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}
